/*! text-page.less 社群产品-文本页面 */

// 单行文本居中 多行居左
 @padding-LR: @26px;
.text-single-center {
	display: block;
	width: 100%;
	text-align: center;
	> span {
		display: inline-block;
		text-align: left;
	}
}
.text-page {
	color: #000;
	font-size: @32px;
	line-height: @44px;
	min-height: 100%;
	background-color: #fff;
	padding-bottom: @20px;
	h3 {
		font-size: @44px;
		text-indent: 0;
		line-height: @64px;
		font-weight: 700;
		padding: @10px @padding-LR;
	}
	img {
		width: 100%;
		padding: @20px 0;
		vertical-align: top;
	}
	p {
		text-indent: 2em;
		padding: 0 @padding-LR;
	}
	.time {
		padding: 0 @padding-LR;
		line-height: 1;
		font-size: @24px;
		font-weight: normal;
		color: #b2b2b2;
		padding-bottom: @44px;
	}
}
