/*! normalize.css v3.0.3 | MIT License | github.com/necolas/normalize.css */
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
/*! component/common.less  */
html {
  width: 100%;
  height: 100%;
  font-size: 100%;
  /*浏览器默认字体大小*/
  font-family: 'PingFang SC', 'Helvetica Neue', Helvetica, 'Nimbus Sans L', Arial, 'Liberation Sans', 'Hiragino Sans GB', 'Source Han Sans CN Normal', 'Microsoft YaHei', 'Wenquanyi Micro Hei', 'WenQuanYi Zen Hei', 'ST Heiti', SimHei, 'WenQuanYi Zen Hei Sharp', sans-serif;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-overflow-scrolling: touch;
}
body {
  width: 100%;
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-overflow-scrolling: touch;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
[v-cloak] {
  display: none;
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
  outline: 0;
}
/* 去除 ios 点击之后的背景 */
a,
input,
label {
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
body,
ul {
  margin: 0;
  padding: 0;
}
/********************************************************/
img {
  max-width: 100%;
  display: block;
  height: auto;
  outline: 0;
  vertical-align: top;
  border: 0;
}
img[src=''],
img:not([src]) {
  opacity: 0;
}
/*! text-page.less 社群产品-文本页面 */
.text-single-center {
  display: block;
  width: 100%;
  text-align: center;
}
.text-single-center > span {
  display: inline-block;
  text-align: left;
}
.text-page {
  color: #000;
  font-size: 0.42666667rem;
  line-height: 0.58666667rem;
  min-height: 100%;
  background-color: #fff;
  padding-bottom: 0.26666667rem;
}
.text-page h3 {
  font-size: 0.58666667rem;
  text-indent: 0;
  line-height: 0.85333333rem;
  font-weight: 700;
  padding: 0.13333333rem 0.34666667rem;
}
.text-page img {
  width: 100%;
  padding: 0.26666667rem 0;
  vertical-align: top;
}
.text-page p {
  text-indent: 2em;
  padding: 0 0.34666667rem;
}
.text-page .time {
  padding: 0 0.34666667rem;
  line-height: 1;
  font-size: 0.32rem;
  font-weight: normal;
  color: #b2b2b2;
  padding-bottom: 0.58666667rem;
}
